import AnimatedNumber from "animated-number-react";
export default function HomeSixSection(){
   const formatValue = (value) => value.toFixed(0);
    return(
        <div className='HomeSixSection'>
            <h3>We Put People First.</h3>
            <div className='service-rec'></div>
            <div style={{display:'flex', margin:'auto', justifyContent:'center', alignContent:'center'}}>
                <div style={{margin: 'auto', padding:'0px 40px'}}>
                    <h4 style={{fontSize:'24px'}}>Properties Sold</h4>
                    <AnimatedNumber
                    className='animated-number'
                    value={4200}
                    easing={"linear"}
                    formatValue={formatValue}
                    duration={50000}
                    delay={10000}
                    />
                </div>
                <div style={{margin: 'auto', padding:'0px 40px'}}>
                    <h4 style={{fontSize:'24px'}}>Total Properties Available</h4>
                    <AnimatedNumber
                    className='animated-number'
                    value={1500}
                    easing={"linear"}
                    formatValue={formatValue}
                    duration={50000}
                    delay={10000}
                    />
                </div>
            </div>
        </div>
    );
}