import { useState } from "react";
import {MdKeyboardArrowDown} from "react-icons/md";
import { useSpring, animated } from 'react-spring';
export default function HomeSecondSection(){
    const [flip, set] = useState(true);
    const props = useSpring({ loop: true, to: { marginTop: 0 }, from: { marginTop: 30 }, onRest: () => set(!flip) })
    return(
        <div class='home-body-main-two'>
            <div className='home-body-main-two-circle'>
                <animated.div style={props}>
                    <MdKeyboardArrowDown className='home-body-main-two-arrow' style={{fontSize: '60px', color: "white"}}/>
                </animated.div>
            </div>
            <div className='homeSecondSection-two'>
                <div className='homeSecondSection-two-header'>
                    <h3>Our Services</h3>
                    <div className='service-rec'></div>
                </div>
                <div className='homeSecondSection-two-sub-header'>
                    <h3>The smartest way to buy a home</h3>
                </div>
            </div>
        </div>
    );
}