function Contacts(){
    return (
        <div>

            <form name="contact-form" role="form" id="contact-form">
                <div class="container">
                    <h2>Contact us</h2>
                    <h5 style={{marginBottom: "20px;"}}>Complete the form or e-mail <a href="mailto:help@apd.com.ng">help@apd.com.ng</a>.</h5>
                    <div className="row100">
                        <div className="col">
                            <div className="inputBox">
                                <input type="text" className="form-control name" required="required"/>
                                <span className="text">Name</span>
                                <span className="line"></span>
                            </div>
                        </div>
                        <div className="col">
                            <div className="inputBox">
                                <input type="text" className="form-control name" required="required" />
                                <span className="text">Company</span>
                                <span className="line"></span>
                            </div>
                        </div>
                </div>
                <div className="row100">
                    <div className="col">
                        <div className="inputBox">
                            <input type="text" required="required" className="form-control email" />
                            <span className="text">Email</span>
                            <span className="line"></span>
                        </div>
                    </div>
                    <div className="col">
                        <div className="inputBox">
                            <input type="text"required="required" className="form-control phonenumber" />
                            <span className="text">Phone Number</span>
                            <span className="line"></span>
                        </div>
                    </div>
                </div>
                <div className="row100">
                    <div className="col">
                        <div className="inputBox textarea">
                            <textarea required="required" className="form-control message"></textarea>
                            <span className="text">Type your message here...</span>
                            <span className="line"></span>
                        </div>
                    </div>
            </div>
            <div className="row100">
                    <div className="col">
                        <input type="submit" value="SEND MESSAGE" id="submit-button" />
                    </div>
                </div>
            </div>
    </form>
        <p className="contact-us-below-p">You can also call <span>(+234) </span><a href="tel:+2348078127528">807 812 7528</a></p>



        </div>
    );
  };
  
export default Contacts;